import React, { useState } from 'react';
import Sresult from './Sresult';
import './App.css';


function App() {
  const [value,changevalue]=useState("");
  const[results,setResults]=useState([])
  const fetchimages=()=>{
    fetch(`https://api.unsplash.com/search/photos?client_id=nnKeNjkQVgrJpOa6OMxLXwfTmq2-sZok27uB_Ke2tYc&query=${value}&orientation=squarish`)
    .then(res=>res.json())
    .then(data=>{
      setResults(data.results);
    })
  }
  function change(event){
     changevalue(event.target.value);
  }
  return (
    <>
    <div className="App">
    <h3>Unsplash Clone</h3>
      <div className="mydiv">

        <span>Search</span>
        <input type="text"value={value}onChange={change}/>
        <button onClick={()=>{fetchimages()}}>Send</button>
      </div>
      <div className="gallery">
        {
          results.map((item)=>{
            return <img className="item" key={item.id}src={item.urls.regular}/>
          })
        }
      </div>
    </div>
    </>
  );
}

export default App;
